<template>
	<div>
		<div v-html="list" class="box">
			
			
		</div>
		
		
		
		<div style="width: 100%;height: 1rem;"></div>
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		created(){
			
			this.getData()
		},
		data(){
			return {
				  list:""
			}
		},
		methods:{
			getData(){
				var obj={
					"a":2
				}
				this.$Api.Setting(obj).then((res)=>{
					console.log(res)
					this.list=res.q.content
				})
			}
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.box{
	padding: 0.3rem;
	font-size: 0.26rem;
}

</style>


